import { Container } from '../../modules/wrapper';

import NewStair01 from './new-stair-01/new-stair-01';
import NewStair02 from './new-stair-02/new-stair-02';
import NewStair03 from './new-stair-03/new-stair-03';
import OldStair from './old-stair/old-stair';

const components = {
  NewStair01,
  NewStair02,
  NewStair03,
  OldStair,
};

export default class Menu extends Container {
  constructor(app) {
    super(app, components);
    this.currentStair = this.components['OldStair'];
    this.bind();
  }
  data() {
    return {
      visible: true,
      x: 0,
      y: 0,
      z: 0
    }
  }
  init() {
    super.init();
  }
  bind() {
    this.app.eventEmitter.on('pointertapCell', (component) => {
      this.change(component.id);
    });
  }
  change(id) {
    this.hide(this.currentStair);
    switch (id) {
      case 'cell01':
        this.show(this.components['NewStair01']);
        break;
      case 'cell02':
        this.show(this.components['NewStair02']);
        break;
      case 'cell03':
        this.show(this.components['NewStair03']);
        break;
    }
  }
  show(component) {
    component.alpha = 0;
    component.visible = true;
    component.y = component.y - 100;
    const props = [
      {name: 'alpha', value: 1, time: 1000},
      {name: 'y', value: 100, time: 1000}
    ];
    this.app.animate.$__animate(component, props, () => {
      this.currentStair = component;
    });
  }
  hide(component) {
    component.alpha = 1;
    const props = [
      {name: 'alpha', value: -1, time: 1000},
      {name: 'y', value: 100, time: 1000}
    ];
    this.app.animate.$__animate(component, props, () => {
      component.visible = false;
      component.y = component.y - 100;
    });
  }
}
