import { Container } from '../../modules/wrapper';

import Cell01 from './cell/cell-01/cell-01';
import Stair01 from './stair/stair-01/stair-01';
import Cell02 from './cell/cell-02/cell-02';
import Stair02 from './stair/stair-02/stair-02';
import Cell03 from './cell/cell-03/cell-03';
import Stair03 from './stair/stair-03/stair-03';
import Ok from './ok/ok';

const components = {
  Cell01,
  Stair01,
  Cell02,
  Stair02,
  Cell03,
  Stair03,
  Ok
};

export default class Menu extends Container {
  constructor(app) {
    super(app, components);
    this.bind();
  }
  data() {
    return {
      visible: false,
      x: 842,
      y: 6,
      z: 10
    }
  }
  init() {
    super.init();
  }
  bind() {
    this.app.eventEmitter.on('pointertapIconHammer', () => {
      this.show();
    });
  }
  show(){
    this.app.animate.fadeIn(this, 300, () => {
      this.visible = true;
    });
  }
}
