import * as PIXI from 'pixi.js';
import { Sprite } from '../../../modules/wrapper';

export default class Cell extends Sprite {
  constructor(app) {
    super(app);
    this.bind();
  }
  data() {
    return {
      visible: true,
      path: './assets/menu/cell.png',
      hover: './assets/menu/cell_choosed.png',
      x: 0,
      y: 0,
      z: 0,
    };
  }
  init() {
    super.init();
    this.bol = false;
    this.defaultTexture = this.texture;
    this.activeTexture = this.setActiveTexture();
    this.buttonMode = true;
    this.interactive = true;
  }
  bind() {
    this.on('pointertap', () => {
      this.app.eventEmitter.emit('pointertapCell', this);
    });
  }
  pointerover() {
    this.texture = this.activeTexture;
  }
  pointerout() {
    this.texture = this.defaultTexture;
  }
  setActiveTexture() {
    const { hover } = this.data();
    return PIXI.Texture.from(hover);
  }
}
