import * as PIXI from 'pixi.js';
import utils from './utils';

const data = {
  visible: false,
  path: '',
  x: 0,
  y: 0,
  z: 0
};

const init = (_this) => {
  const { visible, x, y, z } = _this.data();
  _this.position.set(x, y);
  _this.zIndex = z;
  _this.visible = visible;
};

class Container extends PIXI.Container {
  constructor(app, components) {
    super();
    this.app = app;
    this.components = components;
    this.init();
  }
  data() {
    return data;
  }
  init() {
    init(this);
    const { components, resources } = utils.generator(this.app, this.components);
    this.components = components;
    this.resources = resources;
    utils.addChild(this, components);
    this.sortChildren();
  }
}

class Sprite extends PIXI.Sprite {
  constructor(app) {
    super();
    this.app = app;
    this.init();
  }
  data() {
    return data;
  }
  init() {
    init(this);
    const { path } = this.data();
    this.texture = PIXI.Texture.from(path);
  }
}

class Graphics extends PIXI.Graphics {
  constructor(app) {
    super();
    this.app = app;
    this.init();
  }
  data() {
    return data;
  }
  init() {
    init(this);
  }
}

export { Container, Sprite, Graphics };
